.popup-services {
  position: absolute;
  transform: translateX(100%);
  right: 0;
  top: 0;
  max-width: 504px;
  width: 100%;
  background: #fff;
  z-index: 2001;
  transition: all .3s ease-in-out;
  filter: blur(4x);
  height: 100%;
}

.popup-services .scroll {
  height: 100%;
  overflow-y: scroll;
  -ms-overflow-style: none;
  scrollbar-width: none;
}

.popup-services .scroll::-webkit-scrollbar {
  display: none;
}

.popup-services.show {
  transform: translateY(0);
  filter: blur(0);
}

.popup-services .titlte-popup {
  padding: 30px 46px;
  font-size: 26px;
  font-weight: 500;
  border-bottom: 1px solid #f0f0f0;
}

.popup-services .wrap {
  padding: 0 46px;
}

.popup-services .list-name {
  padding-top: 32px;
  font-size: 16px;
  font-weight: 500;
}

.popup-services .wrap-list {
  position: relative;
}

.popup-services .wrap-list:before {
  content: '';
  width: calc(100% - 24px);
  height: 70px;
  position: absolute;
  bottom: 0;
  right: 24px;
  background: rgba(255, 255, 255, 0.6);
  z-index: 4;
}

.popup-services .list {
  margin-top: 13px;
  margin-right: 15px;
  max-height: 410px;
  overflow-y: hidden;
}

.popup-services .list:hover {
  overflow-y: auto;
  overflow-x: hidden;
  -webkit-overflow-scrolling: touch;
  -moz-overflow-scrolling: touch;
  -ms-overflow-scrolling: touch;
  -o-overflow-scrolling: touch;
  overflow-scrolling: touch;
}

.popup-services .list::-webkit-scrollbar {
  -webkit-appearance: none;
  width: 9px;
}

.popup-services .list::-webkit-scrollbar-thumb {
  border-radius: 2px;
  background: #16ca56;
  box-shadow: 11px 11px 21px 0 rgba(22, 202, 86, 0.18);
}

.popup-services .list li {
